.fadeIn {
    animation: fadeIn 3s;
    position: sticky;
    position: -webkit-sticky;
    bottom: 0;
}

@keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
  }
.fadeOut {
    opacity: 0;
    transition: opacity 1s ease;
    position: sticky;
    position: -webkit-sticky;
    bottom: 0;
}